.prev,.next{
  position: absolute;
  top: 30%;
  -webkit-transform: translateY(-50%);
          transform: translateY(-50%);
  background: #868585;
  color: rgb(0, 0, 0);
  width: 1.75rem;
  height: 2.25rem;
  display: grid;
  place-items: center;
  border-color: transparent;
  font-size: 1rem;
  border-radius: 2px;
  border: 2px solid transparent;
  cursor: pointer;
  transition: all 0.3s linear ;
  visibility: hidden;
}
.prev {
    left: 0;
  }
  .next {
    right: 0;
  }
  
.homeContainer:hover .prev , 
.homeContainer:hover .next{
    visibility: visible;
}

article {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    opacity: 0;
    transition: all 0.3s linear;
  }
article.activeSlide {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
  }
  article.lastSlide {
    -webkit-transform: translateX(-100%);
            transform: translateX(-100%);
  }
  article.nextSlide {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }


