.flipper {
  padding: 30px;
  width: 500px;
  max-width: 90%;
  /* height:auto; */
  /* position: relative; */
    background: #f5f5f5;
    border-radius: 20px;
	
	transition: 0.6s;
	-webkit-transform-style: preserve-3d;
	        transform-style: preserve-3d;
}

.front, .back {
  /*background-color: rgba(0,0,0,.3);*/
  position: absolute;
    padding:10px 30px;
  top: 0;
  left: 0;
  right: 0;
	
	-webkit-backface-visibility: hidden;
	
	        backface-visibility: hidden;
}

.front{
	z-index: 2;
	/* for firefox 31 */
	-webkit-transform: rotateY(0deg);
	        transform: rotateY(0deg);
}

.back{
    -webkit-transform: rotateY(180deg);
            transform: rotateY(180deg);
}
.flip{
		-webkit-transform: rotateY(180deg);
		        transform: rotateY(180deg);
}
.backflip{
    -webkit-transform: rotateY(0deg);
            transform: rotateY(0deg);
}
.flipper input{
    width:100%;
    margin-bottom:15px;
    height:40px;
    box-sizing: border-box;
    padding:10px;
}

.title{
  text-align: center;
